.banner {
   padding: 153px 0 149px;
   background: url(../../../../static/images/croissants.png) no-repeat center center / cover, rgba(0, 0, 0, 1);
   position: relative;

   &::after {
      content: "";
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.2);
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
   }

   .container {
      max-width: 950px;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      padding: 0 15px;
      margin: 0 auto;
      position: relative;
      z-index: 2;
   }

   &__play {
      width: 160px;
      height: 160px;
      position: relative;

      img {
         cursor: pointer;
         position: relative;
         z-index: 3;
      }

      &::before {
         content: "";
         width: 190px;
         height: 185px;
         background: url(../../../../static/images/crumbs4.svg) no-repeat center / contain;
         position: absolute;
         top: -98px;
         left: -80px;
         z-index: 2;
      }
   }

   &__hat {
      max-width: 473px;
      display: flex;
      flex-direction: column;
      gap: 18px;

      h2 {
         color: #fff;
      }
   }
}

@media screen and (max-width: 1199px) {
   .banner {
      padding: 50px 0;

      .container {
         padding: 0 30px;
      }

      &__play {
         width: 130px;
         height: 130px;
      }
   }
}

@media screen and (max-width: 767px) {
   .banner {
      .container {
         flex-direction: column-reverse;
         align-items: center;
         gap: 50px;
      }

      &__play {
         &::before {
            display: none;
         }
      }
   }
}